<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-secondary shadow border-0">
        <div class="card-body px-lg-5 py-lg-5">
          <div class="text-center text-muted mb-4">
            <small>Sign in with credentials</small>
          </div>
          <form role="form" @submit.prevent.stop="login" novalidate>
            <base-input
              :class="['input-group-alternative mb-3', { 'is-invalid': errors.email }]"
              placeholder="Email"
              addon-left-icon="ni ni-email-83"
              v-model="email"
              :disabled="loading || success"
              :error="errors.email"
            ></base-input>

            <base-input
              :class="['input-group-alternative', { 'is-invalid': errors.password }]"
              placeholder="Password"
              type="password"
              addon-left-icon="ni ni-lock-circle-open"
              v-model="password"
              :disabled="loading || success"
              :error="errors.password"
            ></base-input>

            <base-checkbox
              class="custom-control-alternative"
              :disabled="loading || success"
              v-model="remember"
            >
              <span class="text-muted">Remember me</span>
            </base-checkbox>
            <div class="text-center">
              <base-button
                type="primary"
                nativeType="submit"
                class="my-4"
                :disabled="loading || success"
                block
              >Sign in</base-button>
            </div>
          </form>
        </div>
        <base-progress
          v-show="loading"
          :height="6"
          :show-percentage="false"
          :value="100"
          striped
          animated
        />
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { validateEmail, validatePassword } from "@/utils/validate";
export default {
  name: "login",
  data: () => {
    return {
      email: "",
      password: "",
      loading: false,
      remember: true,
      success: false,
      errors: {}
    };
  },
  methods: {
    async login() {
      let valid = true;
      this.errors = {};
      this.$notifications.clear();

      const validEmail = validateEmail(this.email);
      if (!validEmail.valid) {
        valid = false;
        Vue.set(this.errors, "email", validEmail.error);
      }

      const validPassword = validatePassword(this.password);
      if (!validPassword.valid) {
        valid = false;
        Vue.set(this.errors, "password", validPassword.error);
      }

      if (!valid) {
        return;
      }

      this.loading = true;
      return await this.$store
        .dispatch("login", {
          email: this.email,
          password: this.password,
          remember: this.remember
        })
        .then(success => {
          this.success = success;
          this.$notify({
            message: "You have logged in successfully, redirecting...",
            type: "success",
            timeout: 3000
          });
          setTimeout(() => this.$router.push({ name: "dashboard" }), 3000);
        })
        .catch(error => {
          this.$notify({
            message: error.message,
            type: "danger",
            timeout: 10000
          });
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
</script>
